<template>
  <div class="question-actions">
    <router-link
      :to="{ name: 'question-editor', params: { slug: slug } }"
      ><v-btn
      class="mx-2"
      fab
      dark
      small
      color="primary">
      <v-icon dark>
          mdi-pencil
      </v-icon>
    </v-btn>
    </router-link>

    
    <v-btn
      @click="deleteQuestion"
      class="mx-2"
      fab
      dark
      small
      color="pink"
    >
      <v-icon dark>
        mdi-delete
      </v-icon>
    </v-btn>
 
  </div>  
</template>

<script>
import { apiService } from "@/common/api.service.js";
export default {
  name: "QuestionActions",
  props: {
    slug: {
      type: String,
      required: true
    }
  },
  methods: {
    async deleteQuestion() {
      let endpoint = `/api/questions/${this.slug}/`;
      try {
        await apiService(endpoint, "DELETE");
        this.$router.push("/");
      }
      catch (err) {
        console.log(err);
      }
    }
  }
}
</script>
